import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getBranchs } from 'services/Candidate';

const { Option } = Select;

@Form.create({ name: 'formSearchIndividual' })
class FormSearchIndividual extends Component {
  state = {
    branchs: [],
  };

  componentDidMount() {
    this.getBranchs();
  }

  getBranchs = async () => {
    const res = await getBranchs({ isPagingEnabled: false });
    this.setState({ branchs: res.data.items });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    const { dataFormSearch } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      dataFormSearch(values);
    });
  };

  render() {
    const { branchs: studyFields } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={4}>
            <Form.Item label="Số BD">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Tên">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Giới tính">
              {getFieldDecorator('gender', {})(
                <Select>
                  <Option value="true">Nam</Option>
                  <Option value="false">Nữ</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngành TS">
              {getFieldDecorator('studyFieldId', {})(
                <Select>
                  {studyFields.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Tổng điểm">{getFieldDecorator('sumGrade', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={() => this.handleReset()} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchIndividual;



