import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnLock } from 'components/common/button';
import { Card, Row, Table, Col, Button, message } from 'antd';
import {
  getListCandidate,
  getListStudent,
  addStudentInClass,
  lockStudentInClass,
} from 'services/Candidate';
import FormSearchStudent from './FormSearchIndividual';
import FormSearchClass from './FormSearchClass';

class ClassDivisionPage extends Component {
  state = {
    dataStudent: [],
    dataClass: [],
    loadingStudent: false,
    loadingClass: false,
    disableSave: true,
    classId: '',
    paramSearchStudent: {},
    paramSearchClass: {},
    isDirty: false,
  };

  handleSearch = async value => {
    const { classId, paramSearchClass, isDirty } = this.state;
    await this.setState({ loadingStudent: true, paramSearchStudent: value });
    if (classId !== '' && isDirty) {
      await this.setState({ isDirty: false });
      this.searchClass(paramSearchClass);
    }
    const dataTable = await getListCandidate(value, false);
    const students = dataTable.data.items;
    students.forEach(student => {
      student.birthday = student.dob;
      student.sbd = student.code;
    });
    await this.setState({ loadingStudent: false });
    this.setState({
      dataStudent: dataTable.data.items,
    });
  };

  handleStudent = record => {
    const { classId } = this.state;
    if (classId !== '') {
      this.setState({ disableSave: false });
      const { dataClass, dataStudent } = this.state;
      const filterStudent = dataStudent.filter(item => item.id !== record.id);
      record.candidateId = record.id;
      dataClass.push(record);
      this.setState({ dataClass, dataStudent: filterStudent, isDirty: true });
    } else {
      message.success('Vui lòng chọn Lớp');
    }
  };
  handleDeleteStudent = record => {
    this.setState({ disableSave: false });
    const { dataClass } = this.state;
    const filterClass = dataClass.filter(item => item.id !== record.id);
    this.setState({ dataClass: filterClass, isDirty: true });
  };

  searchClass = async dataForm => {
    const { paramSearchStudent, isDirty } = this.state;
    await this.setState({
      loadingClass: true,
      disableSave: true,
      paramSearchClass: dataForm,
    });
    if (isDirty) {
      await this.setState({ isDirty: false });
      this.handleSearch(paramSearchStudent);
    }
    const dataSearchClass = [];
    const dataStudent = await getListStudent(dataForm.classId);
    this.setState({
      classId: dataForm.classId,
    });
    const dataStudentItem = dataStudent.data.items;
    dataStudentItem.forEach(student => {
      student.sbd = student.candidateCode;
      student.fullname = `${student.firstname} ${student.lastname}`;
      student.birthday = student.dob;
    });
    dataStudentItem.map(item => dataSearchClass.push(item));
    await this.setState({ loadingClass: false });
    this.setState({
      dataClass: dataSearchClass,
    });
  };

  saveClass = async () => {
    const { classId, dataClass, paramSearchClass } = this.state;
    const listClass = [];
    if (dataClass.length > 0) {
      dataClass.map(student => listClass.push(student.candidateId));
    }
    await addStudentInClass(classId, listClass);
    message.success('Lưu thành công');
    this.searchClass(paramSearchClass);
  };

  lockStudent = async () => {
    const { classId, dataClass, paramSearchClass } = this.state;
    const listClass = [];
    if (dataClass.length > 0) {
      dataClass.map(student => listClass.push(student.candidateId));
    }
    await lockStudentInClass(classId, listClass);
    message.success('Khoá thành công');
    this.searchClass(paramSearchClass);
  };

  render() {
    const { dataStudent, dataClass, loadingStudent, loadingClass, disableSave } = this.state;
    const columnsTableChooses = [
      {
        title: 'Thao tác',
        dataIndex: 'thaotac',
        render: (text, record) => (
          <Fragment>
            {record.isLocked ? (
              ''
            ) : (
              <Button onClick={() => this.handleDeleteStudent(record)} type="danger">
                Xóa
              </Button>
            )}
          </Fragment>
        ),
      },
      {
        title: 'Số BD',
        dataIndex: 'sbd',
        key: 'sbd',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'birthday',
        key: 'birthday',
      },
    ];
    const columns = [
      {
        title: 'Thao tác',
        dataIndex: 'thaotac',
        render: (text, record) => (
          <Fragment>
            <Button onClick={() => this.handleStudent(record)} type="primary">
              Chọn
            </Button>
          </Fragment>
        ),
      },
      {
        title: 'Số BD',
        dataIndex: 'sbd',
        key: 'sbd',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'birthday',
        key: 'birthday',
      },
    ];
    return (
      <Fragment>
        <Card title="Phân lớp SV">
          <Row>
            <Col span={24}>
              <FormSearchStudent dataFormSearch={this.handleSearch} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered loading={loadingStudent} dataSource={dataStudent} columns={columns} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách đã chọn">
          <Row>
            <Col span={24}>
              <FormSearchClass formDataSearch={this.searchClass} />
            </Col>
            <Col span={24}>
              <Table
                bordered
                loading={loadingClass}
                dataSource={dataClass}
                columns={columnsTableChooses}
              />
            </Col>
            <Col offset={8} span={12}>
              {dataClass.length ? (
                <div>
                  <BtnSave disabled={disableSave} onClick={() => this.saveClass()} />
                  <BtnLock onClick={() => this.lockStudent()} />
                </div>
              ) : null}
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassDivisionPage />
  </Page>
);



