import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getListCandidate = (paramsSearch, isPagingEnabled = false) =>
  request({
    prefix,
    url: '/Candidate',
    params: {
      code: paramsSearch.code || null,
      fullname: paramsSearch.fullname || null,
      gender: paramsSearch.gender || null,
      studyFieldId: paramsSearch.studyFieldId || null,
      sumGrade: paramsSearch.sumGrade || null,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getCandidate = (id, code) =>
  request({
    url: `/Candidate/GetById/${id}/${code}`,
    method: 'GET',
  });
export const deleteCandidate = id =>
  request({
    url: `/Candidate/${id}`,
    method: 'DELETE',
  });
export const updateCandidate = (id, values) =>
  request({
    url: `/Candidate/${id}`,
    data: {
      id: values.id,
      label: values.code,
      sfId: null,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: values.trainTypeId || '',
    },
    method: 'PUT',
  });

export const createCandidate = values =>
  request({
    url: '/Candidate',
    data: {
      trainTypeId: values.trainTypeId,
      prizeClassId: values.prizeClassId,
      amount: values.amount,
    },
    method: 'POST',
  });

export const listPrizeClassification = () =>
  request({
    url: '/PrizeClassification',
    params: {
      isPagingEnabled: false,
    },
    method: 'GET',
  });

export const getListStudent = classId =>
  request({
    prefix,
    url: '/Student',
    params: {
      classId,
      isPagingEnabled: false,
    },
    method: 'GET',
  });

export const addStudentInClass = (classId, listStudent) =>
  request({
    prefix,
    url: '/ClassEnrollment',
    data: {
      classId,
      candidateIds: listStudent,
    },
    method: 'POST',
  });
export const lockStudentInClass = (classId, listStudent) =>
  request({
    prefix,
    url: `/ClassEnrollment/Class/${classId}/Candidate`,
    data: listStudent,
    method: 'PUT',
  });

export const filterCandidateForEnrollment = ({
  code,
  fullname,
  testGroup,
  studyField,
  enrolledRecipient,
  certTypeId,
  trainTypeId,
  yearEduId,
  note,
  fromDate,
  toDate,
  withDocument,
  withoutDocument,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Candidate/FilterCandidateForEnrollment',
    method: 'GET',
    params: {
      code,
      fullname,
      testGroup,
      studyField,
      enrolledRecipient,
      certTypeId,
      trainTypeId,
      yearEduId,
      note,
      fromDate,
      toDate,
      withDocument,
      withoutDocument,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

  export const receiveEnrollNote = ({
    candidateId = 0,
    code,
    enrolledDate,
    enrolledById,
    trainTypeId,
    certTypeId,
    studyFieldId,
    userInputId,
    paperList = [],
    classId,
  }) =>
   request({
     prefix,
     url: '/Candidate/ReceiveEnrollNote',
     method: 'POST',
     data: {
      candidateId,
      code,
      enrolledDate,
      enrolledById,
      trainTypeId,
      certTypeId,
      studyFieldId,
      userInputId,
      paperList,
      classId,
     },
   });

export const exportEnrollNote = ({
  code,
  fullname,
  testGroup,
  studyField,
  enrolledRecipient,
  certTypeId,
  trainTypeId,
  yearEduId,
  note,
  fromDate,
  toDate,
  withDocument,
  withoutDocument,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: '/Candidate/ExportEnrollNote',
    method: 'POST',
    data: {
      code,
      fullname,
      testGroup,
      studyField,
      enrolledRecipient,
      certTypeId,
      trainTypeId,
      yearEduId,
      note,
      fromDate,
      toDate,
      withDocument,
      withoutDocument,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });

export const deleteStudentFromClass = ({ classId, studentId } = {}) =>
  request({
    prefix,
    url: `/ClassEnrollment/Class/${classId}/Student/${studentId}`,
    method: 'DELETE',
    params: {},
    data: {},
  });

export const getBranchs = ({
  label,
  trainTypeId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix: '/api/categories',
    url: '/EnrollStudyField',
    method: 'GET',
    params: { label, trainTypeId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



export const exportCandicateSummaryReport = candidateId =>
  requestDownload({
    prefix,
    url: `/Candidate/${candidateId}/ExportReportSummarypaperCandidate`,
    method: 'POST',
    defaultFileName: 'ReportSummarypaperCandidate.pdf',
  });


